@font-face {
  font-family: 'TheJamsil5Bold';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2302_01@1.0/TheJamsil5Bold.woff2') format('woff2');
  font-weight: 700;
  font-style: normal;
}

footer{
  border-top: 1px solid #ddd;
  width: 100%;
  height: 110px;
  position: fixed;
  padding-bottom: 20px;
  box-sizing: border-box;
  bottom: 0%;
  left: 0%;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
footer a{
  font-family: 'TheJamsil5Bold';
  font-size: 13px;
  font-weight: bold;
  text-decoration: none;
  color: #333;
}
footer div svg{
  fill: #659F7C;
  width: 27px;
  height: 27px;
  margin-bottom: 8px;
}
footer div{
  width: 65px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}