#DaguePeople table {
  border-collapse: collapse;
  margin: 10% auto;
}

#DaguePeople table {
  border-collapse: collapse;
  margin: 10% auto;
  background-color: transparent;
  transform: scale(1);
  border-radius: 3px;
  overflow: hidden;
}

#DaguePeople th,
#DaguePeople td {
  background-color: white;
  text-align: center;
  padding: 10px;
  font-size: 13px;
  width: 60px;
  border: 2px solid white;
}

.null{
  display: none;
}
#DaguePeople th {
  font-weight: bold;
  color: white;
  background-color: transparent;
}

#DaguePeople{
  overflow: hidden;
  background-color: #3cb371;
  width:100%;
  padding: 30px;
  box-sizing: border-box;
  height: calc(100vh);
  position: relative;
}
@keyframes swing{
  0%{
    transform: translate(-50%,0%) rotate(-50deg);
  }
  100%{
    transform: translate(-50%,0%) rotate(50deg);
  }
}
#swing{
  animation: swing 4s infinite alternate;
  position: absolute;
  right: -20%;
  bottom: 17%;
  width: 50vw;
  height: 50vw;
  transform: translate(-50%,0%);
}
#DaguePeople h1{
  color: white;
  text-align: center;
} 

#DaguePeople h1 span{
  font-size: 13vw;
  margin-right: 4vw;

}
