#DaeguNews{
  padding: 40px;
  box-sizing: border-box;
  width: 100%;
  height: 100vh;
}
#DaeguNews h1{
    background-color: #3cb371;
    width: 80px;
    padding: 10px;
    height: 80px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-size: 40px;
    box-shadow: 15px 10px 0px 0px darkgreen,-15px -10px 0px 0px seagreen;
}
