
@font-face {
  font-family: 'TTWanjudaedunsancheB';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2304-2@1.0/TTWanjudaedunsancheB.woff2') format('woff2');
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: 'GmarketSansMedium';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/GmarketSansMedium.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

main{
  position: relative;
  color: white;
  box-sizing: border-box;
  padding: 20px;
  padding-top: 1vh;
  width: 100%;
  height: calc(100vh - 100px);
  background-color: #3cb371;
}
#popup #logo{
  box-sizing: border-box;
  width: 100%;
  margin-top: 40px;
  border-radius: 10px;
  background-color: white;
  padding: 20px 60px;
  box-shadow: 0px 7px 0px 0px seagreen;
}


main img{ 
  width: 90px;
}

#popup{
  padding: 40px;
  padding-top: 80px;
  background-color: #3cb371;
  box-sizing: border-box;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0%;
  top: 0%;
  transition: 0.35s;
}


@keyframes ani{
  0%{
    transform: rotate(0deg);
  }
  100%{
      transform: rotate(1turn);
  }
}

#popup #close{
  animation: ani 10s infinite alternate;
  width: 50px;
  height: 50px;
  right: 35px;
  top: 35px;
  position: absolute;
}

#popup div{

  box-shadow: 0px 7px 0px 0px seagreen;
  justify-content: space-around;
  align-items: center;
  margin-top: 2vh;
  border-radius: 10px;
  padding: 10px 20px;
  color: black;
  display: flex;
  background-color: white;
}

#menu1 #main_one{
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  margin: 0px auto 0px;
  height: 78vh; 
  justify-content: center;

  box-sizing: border-box;
  align-items: center;

}



#main_one iframe{
  border-radius: 5px;
  transform: scale(0.9);
  position: relative;
  width: 100%;
  height: auto;
  margin-bottom: 10px;
  
}
#menu1 p{
  font-size: 12px;

}

#main_one div{
  transform: scale(0.9);
  border-radius: 10px;
  overflow: hidden;
  position: relative;
  display: flex;
  flex-wrap: wrap;
  box-sizing: border-box;
  width: 90%;
  height: 45%;
  margin-top: -10px;
  background-color: white;
}
#main_one div p:first-child, #main_one div h1{
  font-family: 'GmarketSansMedium';
  font-weight: bold;
  text-align: center;
  font-size: 14px;
  width: 100%;
  height: 40px;
  display: flex;
  padding: 10px 0px 5px 0px;
  justify-content: center;
  color: white;
  background: #16222A;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #3A6073, #16222A);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #3A6073, #16222A); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
#main_one div h1{
  font-size: 24px;
}
#main_one div:nth-of-type(2){
  height: 55%;
}

#main_one div:nth-of-type(2) p{
  width: 90%;
  text-align: center;
  background-color: #3A6073;
  color: white;
  border-radius: 40px;
  height: 50px;
  margin: 10px auto 0px;
  padding: 0px;
  font-size: 13px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}


#menu1  > div{
  margin-bottom: 50px;
}

#menu1 div:nth-of-type(even){
  text-align: right;  
}
