
@font-face {
  font-family: 'TTWanjudaedunsancheB';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2304-2@1.0/TTWanjudaedunsancheB.woff2') format('woff2');
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: 'GmarketSansMedium';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/GmarketSansMedium.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

main{
  position: relative;
  color: white;
  box-sizing: border-box;
  padding: 20px;
  padding-top: 3vh;
  width: 100%;
  height: calc(100vh - 100px);
  background-color: #3cb371;
}


@keyframes ani{
  0%{
    transform: rotate(0deg);
  }
  100%{
      transform: rotate(1turn);
  }
}

#menu3{
  background-color: red;
  overflow: hidden;
  border-radius: 5px;

}

#menu3 > #main_two{
  background-color: white;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  height: 70vh;
  margin-top: -10px;
  overflow: scroll;

}


#main_two div{
  border-radius: 10px;
  position: absolute;
  display: flex;
  flex-wrap: wrap;
  box-sizing: border-box;
  width: 90%;
  height: 80%;
  border: 3px solid #ddd;
  overflow: auto;
}
#menu1 div:nth-of-type(even){
  text-align: right;  
}

#check{
  height: 65px;
  text-align: center;
  background-color:seagreen;
  color: rgb(255, 255, 255);
  display: flex;
  font-size: 27px;
  font-weight: bold;
  align-items: center;
  justify-content: center;
  box-sizing:border-box;

} 

#list{
  overflow: auto;
  color: black;
  text-align: left;
  font-size: 11px;
}
#list > li{
  margin: 30px;
  list-style:circle;
}